@import "variables.scss";

.wrap {
    border: 1px solid rgba($color: $greyColor, $alpha: 0.2);
    border-radius: 5px 5px 0px 0px;
    display: grid;
    align-items: center;
    grid-template-columns: 1fr 4fr 1fr;
    grid-template-areas: "button userInfo avatar";

    &_dark {
        background-color: $darkContainerBGColor;
        border: 1px solid $greyColor;

        .goBackButton {
            color: $lightGreyColor08;

            &:hover {
                background: linear-gradient(to left, $darkContainerBGColor, $lightGreyColor01);
            }
        }

        .name {
            color: #fff;
        }

        .icon {
            margin: 1px 9px 0px 0px;
            height: 16px;
            fill: $lightGreyColor08;
        }
    }
}

.goBackButton {
    height: 100%;
    border-radius: 5px 0px 0px 0px;
    display: flex;
    align-items: center;
    justify-content: start;
    padding-left: 15px;
    cursor: pointer;
    grid-area: button;
    color: $greyColor;

    &:hover {
        background: linear-gradient(to left, $lightGreyColor01, $semiLightGreyColor01);
    }
}

.userInfo {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0px 15px;
    grid-area: userInfo;
}

.imgLink {
    display: block;
    width: 35px;
    margin: 0px 15px 0px auto;
}

.avatar {
    grid-area: avatar;
    height: 35px;
    border-radius: 50%;
    cursor: pointer;
}

.name {
    font-weight: 500;
    padding-right: 10px;
    cursor: pointer;
    color: $mainFontColor;
}

.icon {
    margin: 1px 9px 0px 0px;
    height: 16px;
    fill: $greyColor;
}
