@import "variables.scss";

.inputWrap {
    position: relative;

    &_withIcon {
        padding-left: 23px;
    }

    &_dark {
        .input {
            background-color: $semiDarkBGColor;
            color: $lightGreyColor;
            border: 1px solid $greyColor;

            &::placeholder {
                color: rgba($color: $lightGreyColor, $alpha: 0.8);
            }
        }

        .icon {
            fill: rgba($color: $lightGreyColor, $alpha: 0.8);
        }
    }
}

.input {
    display: block;
    width: 100%;
    padding: 3px 8px;
    font-size: 15px;
    border-radius: 3px;
    border: 1px solid rgba($color: $greyColor, $alpha: 0.15);

    &_login {
        padding: 10px 45px 10px 20px;
        font-size: 16px;
        margin-bottom: 10px;
        border-radius: 5px;
    }

    &_error {
        border: 1px solid rgba($color: $redColor, $alpha: 0.7);
    }
}

.textarea {
    resize: none;
    outline: none;
    width: 100%;
    height: 100%;
    border: 1px solid rgba($color: $greyColor, $alpha: 0.1);
    border-radius: 5px;
    padding: 9px 15px 11px 15px;
    font-size: 15px;
    box-sizing: border-box;

    &_dark {
        border: 1px solid $greyColor;
        background-color: $semiDarkBGColor;
        color: $lightGreyColor;

        &::placeholder {
            color: rgba($color: $lightGreyColor, $alpha: 0.8);
        }
    }

    &::-webkit-scrollbar {
        width: 0px;
    }
}

.textareaError,
.textareaWarning {
    padding: 5px;
    font-size: 14px;
    color: rgba($color: $mainFontColor, $alpha: 0.5);
}

.warningWrap {
    position: absolute;
    top: calc(50% - (#{$loginWarningiconSize} / 2));
    right: 10px;
}

.checkboxWrap {
    display: flex;
    align-items: center;
}

.checkbox {
    height: 18px;
    width: 18px;
    margin-right: 10px;
    border: 1px solid rgba($color: $greyColor, $alpha: 0.15);

    &__label {
        color: rgba($color: $greyColor, $alpha: 0.9);
    }
}

.icon {
    fill: rgba($color: $greyColor, $alpha: 0.65);
    display: block;
    position: absolute;
    top: 2px;
    left: 0px;
    width: 16px;
    height: 16px;
}
